<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(slide, index) in skuImageList" :key="slide.id">
        <img :src="slide.imgUrl" :class="{ active: currentIndex == index }" @mousemove="chagecurrentIndex(index)" >

      </div>
    </div>
    <div class="swiper-button-next"></div>
    <div class="swiper-button-prev"></div>
  </div>
</template>



<script>
import Swiper from 'swiper'
export default {
  name: "ImageList",
  data() {
    return {
      currentIndex: 0,
    }
  },
  props: ['skuImageList'],
  watch: {
    skuImageList() {
      this.$nextTick(() => {
        new Swiper(".swiper-container", {
          //loop: true, // 循环模式选项
          // 垂直切换选项 默认水平
          // direction: 'vertical', 
          autoplay: {
            delay: 1000,
            stopOnLastSlide: false,
            disableOnInteraction: true,
          },
          slidesPerView: 3,
          slidesPerGroup: 1,

          // 如果需要分页器
          // pagination: {
          //   el: ".swiper-pagination",
          //   clickable: true,
          // },

          // 如果需要前进后退按钮
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },

          // 如果需要滚动条
          // scrollbar: {
          //   el: ".swiper-scrollbar",
          // },
        });
      })
    },
  },
  methods: {
    //鼠标点击图片显示高亮
    chagecurrentIndex(index) {
      this.currentIndex = index;
      //通知兄弟组件(zoom),获取当前组件
      this.$bus.$emit('getIndex',this.currentIndex)
    },
  }
}
</script>

<style lang="less" scoped>
.swiper-container {
  height: 56px;
  width: 412px;
  box-sizing: border-box;
  padding: 0 12px;

  .swiper-slide {
    width: 56px;
    height: 56px;

    img {
      width: 100%;
      height: 100%;
      border: 1px solid #ccc;
      padding: 2px;
      width: 50px;
      height: 50px;
      display: block;

      &.active {
        border: 2px solid #f60;
        padding: 1px;
      }

      // &:hover {
      //   border: 2px solid #f60;
      //   padding: 1px;
      // }
    }
  }

  .swiper-button-next {
    left: auto;
    right: 0;
  }

  .swiper-button-prev {
    left: 0;
    right: auto;
  }

  .swiper-button-next,
  .swiper-button-prev {
    box-sizing: border-box;
    width: 12px;
    height: 56px;
    background: rgb(235, 235, 235);
    border: 1px solid rgb(204, 204, 204);
    top: 0;
    margin-top: 0;

    &::after {
      font-size: 12px;
    }
  }
}
</style>