<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag"   >
            <!-- 分类名字 -->
            <li class="with-x" v-if="searchParmas.categoryName">{{ searchParmas.categoryName }}<i
                @click="removeCategoryName">x</i></li>
            <!-- 搜索框面包屑 -->
            <li class="with-x" v-if="searchParmas.keyword">{{ searchParmas.keyword }}<i @click="removekeyword">x</i></li>
            <!-- 品牌面包屑 -->
            <li class="with-x" v-if="searchParmas.trademark">{{ searchParmas.trademark.split(":")[1] }}<i
                @click="removetrademark">x</i></li>
            <!-- 平台售卖属性面包屑 -->
            <li class="with-x" v-for="(attrValue, index) in searchParmas.props" :key="index">{{ attrValue.split(":")[1]
            }}<i @click="removeattr(index)">x</i></li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo="trademarkInfo
        " @attrInfo="attrInfo" />


        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="changeOrder('1')">
                  <a>综合<i v-show="isOne" :class="{ 'el-icon-top': isAsc, 'el-icon-bottom': isDesc }"></i>
                  </a>
                </li>
                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                  <a>价格<i v-show="isTwo" :class="{ 'el-icon-top': isAsc, 'el-icon-bottom': isDesc }"></i> <span
                      v-show="isTwo"></span></a>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="(goods) in goodsList" :key="goods.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${goods.id}`"><img :src="goods.defaultImg" /></router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥ </em>
                      <i>{{ goods.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a target="_blank" href="item.html" title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】">{{ goods.title
                    }}</a>
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a href="success-cart.html" target="_blank" class="sui-btn btn-bordered btn-danger">加入购物车</a>
                    <a href="javascript:void(0);" class="sui-btn btn-bordered">收藏</a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页器  -->
          <Pagination :pageNo="searchParmas.pageNo" :pageSize="searchParmas.pageSize" :total="total" :continues="3"
            @getPageNo="getPageNo" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from './SearchSelector/SearchSelector'
import { mapGetters, mapState } from 'vuex';
export default {
  name: 'Search',
  data() {
    return {
      searchParmas: {
        //一级分类id
        "category1Id": "",
        //二级分类id
        "category2Id": "",
        //三级分类id
        "category3Id": "",
        //分类名字
        "categoryName": "",
        //搜索关键字
        "keyword": "",
        //排序:初始按综合降序排序(desc)
        "order": "1:desc",
        //分页器：当前是第几页
        "pageNo": 1,
        //分页器：每页展示的数据
        "pageSize": 3,
        //平台售卖属性操作带的参数
        "props": [],
        //品牌
        "trademark": ""
      },
    }
  },
  //在组件挂载完毕之前执行一次
  beforeMount() {
    Object.assign(this.searchParmas, this.$route.query, this.$route.params);
  },
  components: {
    SearchSelector
  },
  mounted() {
    //在发送请求之前带给服务器参数【searchParmas参数发生变化有数值带给服务器】
    this.GetData()
  },
  computed: {
    ...mapGetters(['goodsList']),
    isOne() {
      return this.searchParmas.order.indexOf('1') != -1
    },
    isTwo() {
      return this.searchParmas.order.indexOf('2') != -1
    },
    isAsc() {
      return this.searchParmas.order.indexOf('asc') != -1
    },
    isDesc() {
      return this.searchParmas.order.indexOf('desc') != -1
    },
    //获取search模板展示数据产品一共多少数据
    ...mapState({
      total: state => state.search.searchList.total
    })
  },
  methods: {
    //向服务器发送请求获取search模块数据(根据参数不同返回参数不同的数据进行展示)
    GetData() {
      this.$store.dispatch('getSearchList', this.searchParmas);
    },
    //删除分类名字
    removeCategoryName() {
      this.searchParmas.categoryName = undefined;
      this.searchParmas.category1Id = undefined;
      this.searchParmas.category2Id = undefined;
      this.searchParmas.category3Id = undefined;
      this.GetData();
      if (this.$route.params) {
        this.$router.push({ name: "search", params: this.$route.params });
      }
    },
    //删除搜索框输入
    removekeyword() {
      this.searchParmas.keyword = undefined;
      this.GetData();
      //通知兄弟组件Header清除关键字
      this.$bus.$emit('clear');
      if (this.$route.query) {
        this.$router.push({ name: "search", query: this.$route.query });
      }
    },
    //自定义事件回调
    trademarkInfo(trademark) {
      this.searchParmas.trademark = `${trademark.tmId}:${trademark.tmName}`;
      this.GetData();
    },
    //删除品牌信息
    removetrademark() {
      this.searchParmas.trademark = undefined;
      this.GetData();
    },
    //收集平台属性地方回调函数 （自定义事件）
    attrInfo(attrs, attrValue) {
      let props = `${attrs.attrId}:${attrValue}:${attrs.attrName}`
      //去除props数组的重复数据
      if (this.searchParmas.props.indexOf(props) == -1) {
        this.searchParmas.props.push(props);
      }
      this.GetData();
    },
    removeattr(index) {
      this.searchParmas.props.splice(index, 1);
      this.GetData();
    },
    changeOrder(flag) {
      //flag形参，区别用户点击的排序
      let originOrder = this.searchParmas.order;
      let originFalg = this.searchParmas.order.split(":")[0];
      let originSort = this.searchParmas.order.split(":")[1];
      let newOrder = '';
      if (flag == originFalg) {
        //点击综合
        newOrder = `${originFalg}:${originSort == 'desc' ? 'asc' : 'desc'}`;
      } else {
        //点击价格
        newOrder = `${flag}:${'desc'}`
      }
      //重新修改参数中order的值
      this.searchParmas.order = newOrder;
      this.GetData();
    },
    getPageNo(pageNo) {
      this.searchParmas.pageNo=pageNo;
      this.GetData();
    },
  },
  //数据监听
  watch: {
    //监听路由信息是否发生变化，肉发生变化在向服务器发送一次请求
    $route(newValue, oldValue) {
      //再次发送请求前需要再整理一次给服务器的数据
      Object.assign(this.searchParmas, this.$route.query, this.$route.params);
      this.GetData();
      //清空前面几次的分类id
      this.searchParmas.category1Id = undefined;
      this.searchParmas.category2Id = undefined;
      this.searchParmas.category3Id = undefined;
    }
  }
}
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: -5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      .page {
        width: 733px;
        height: 66px;
        overflow: hidden;
        float: right;

        .sui-pagination {
          margin: 18px 0;

          ul {
            margin-left: 0;
            margin-bottom: 0;
            vertical-align: middle;
            width: 490px;
            float: left;

            li {
              line-height: 18px;
              display: inline-block;

              a {
                position: relative;
                float: left;
                line-height: 18px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #e0e9ee;
                margin-left: -1px;
                font-size: 14px;
                padding: 9px 18px;
                color: #333;
              }

              &.active {
                a {
                  background-color: #fff;
                  color: #e1251b;
                  border-color: #fff;
                  cursor: default;
                }
              }

              &.prev {
                a {
                  background-color: #fafafa;
                }
              }

              &.disabled {
                a {
                  color: #999;
                  cursor: default;
                }
              }

              &.dotted {
                span {
                  margin-left: -1px;
                  position: relative;
                  float: left;
                  line-height: 18px;
                  text-decoration: none;
                  background-color: #fff;
                  font-size: 14px;
                  border: 0;
                  padding: 9px 18px;
                  color: #333;
                }
              }

              &.next {
                a {
                  background-color: #fafafa;
                }
              }
            }
          }

          div {
            color: #333;
            font-size: 14px;
            float: right;
            width: 241px;
          }
        }
      }
    }
  }
}
</style>