<template>
  <div class="pagination">

    <button :disabled="pageNo == 1" @click="$emit('getPageNo', pageNo - 1)">上一页</button>
    <button v-if="startNumAndEndNum.start > 1" @click="$emit('getPageNo', 1)" :class="{active:pageNo==1}">1</button>
    <button v-if="startNumAndEndNum.start > 2">···</button>

    <span v-for="(page, index) in startNumAndEndNum.end" :key="index">
      <button v-if="page >= startNumAndEndNum.start" @click="$emit('getPageNo', page)" :class="{active:pageNo==page}">{{ page }}</button>
    </span>

    <button v-if="startNumAndEndNum.end < totalPage - 1">···</button>
    <button v-if="startNumAndEndNum.end < totalPage" @click="$emit('getPageNo', totalPage)" :class="{active:pageNo==totalPage}">{{ totalPage }}</button>

    <button :disabled="pageNo == totalPage" @click="$emit('getPageNo', pageNo + 1)" >下一页</button>

    <button style="margin-left: 30px">共 {{ total }} 条</button>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageNo", "pageSize", "total", "continues"],
  //pageNo：当前页；pageSize：每页的数据个数；total：数据总个数；continues:中间的连续页码
  computed: {
    //总页数
    totalPage() {
      return Math.ceil(this.total / this.pageSize);
    },
    //计算中间连续的起始页码
    startNumAndEndNum() {
      const { continues, pageNo, totalPage } = this;
      let start = 0, end = 0;
      //当中间的连续页码小于continues总页码totalPage
      if (continues > totalPage) {
        //最开始的连续页码为1，末尾的页面为总页数
        start = 1;
        end = totalPage;
      } else {
        //parseInt向下取整
        //开始连续页码
        start = pageNo - parseInt(continues / 2);
        //末尾连续页码
        end = pageNo + parseInt(continues / 2);
        //出现菲正常现象（start为0或负数）
        if (start < 1) {
          start = 1;
          end = continues;
        };
        if (end > totalPage) {
          end = totalPage;
          start = totalPage - continues + 1;
        }
      }
      return { start, end }
    }
  },
  methods: {

  }
}
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;

  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background: rgb(218, 242, 5);
      color: #fff;
    }
  }
}
</style>